<template>
  <div class="zhuq_box">
    <div class="zhuq_one">
      <img src="../assets/zhuq.png" class="zhuq_oneimg">
      <h1 class="zhuq_oneh1">入驻企业</h1>
      <h2 class="zhuq_oneh2">各领域优秀企业达到1000+</h2>
    </div>
    <div class="zhuq_two">
      <ul>
        <li class="zhuq_twoli" v-for="(i,index) in listData">
          <span class="zhuq_twosp">{{index + 1}}</span>
          <b class="zhuq_twob">{{i.companyName}}</b>
        </li>
        <!--<li class="zhuq_twoli">
          <span class="zhuq_twosp">2</span>
          <b class="zhuq_twob">万众网影（北京）科技有限公司</b>
        </li>
        <li class="zhuq_twoli">
          <span class="zhuq_twosp">3</span>
          <b class="zhuq_twob">万众网影（北京）科技有限公司</b>
        </li>-->
      </ul>
    </div>
    <p @click="getList(2)" class="more" v-if="currentPage < totalPage ||!totalPage ">加载更多</p>
    <p class="more" v-else-if="currentPage >= totalPage">没有更多了</p>
  </div>
</template>

<script>
  import '../../static/js/pb.js';
  export default {
        name: "zhuq",
        data () {
          return {
            totalPage: null,
            currentPage: 1,
            listData: []
          };
        },
        mounted () {
          this.getList(1);
          document.querySelector('body').setAttribute('style', 'background-color:#eaeaea')
        },
        beforeDestroy() {
          document.querySelector('body').removeAttribute('style')
        },
        methods: {
          getList (index) {
            if (index === '1') {
              this.$axios.post('/companySet/selectCompanySetListPhone.do',{
                'currentPage': 1
              }).then(res => {
                this.listData =  res.data.data;
                this.totalPage = res.data.totalCount
                console.log(this.listData);
                console.log(this.listData.length);
              })
            } else {
              this.$axios.post('/companySet/selectCompanySetListPhone.do',{
                'currentPage': Number(this.currentPage + 1)
              }).then(res => {
                console.log(res);
                this.listData =  this.listData.concat(res.data.data)
              })
            }

          }
        }
    }
</script>
<style scoped>
  @import '../../static/css/pb.css';
  .zhuq_oneimg{
    width: 100%;
    position: relative;
  }
  .zhuq_one{
    position: relative;
  }
  .zhuq_oneh1{
    font-size:0.5rem;
    font-weight: bold;
    color: #fff;
    position: absolute;
    top: 38%;
    left: 50%;
    transform: translate(-50%,-50%);
    letter-spacing:4px;
    display: table;
  }
  .zhuq_oneh2{
    font-size: 0.18rem;
    color: #fff;
    position: absolute;
    top: 62%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: table;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    padding-top: 0.06rem;
    padding-bottom: 0.06rem;
  }

  .zhuq_two{
    width: 96%;
    margin-left: 2%;
    margin-right: 2%;
    background:#fff;
    display: table;
    border-radius: 6px;
    margin-top: -0.4rem;
    position: relative;
    z-index: 10;
  }
  .zhuq_twosp{
    width: 0.25rem;
    height: 0.25rem;
    font-size: 0.13rem;
    color: #aaaaaa;
    background: #eaeaea;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    display: flex;
    float: left;
    margin-top: 0.12rem;
  }
  .zhuq_twob{
    font-size: 0.15rem;
    color: #333;
    float: left;
    margin-left: 0.1rem;
    margin-top: 0.15rem;
    width:3rem;
  }
  .zhuq_twoli{
    border-bottom: 1px solid #dddddd;
    padding-bottom: 0.12rem;
    display: table;
    width: 94%;
    margin-left:3%;
    margin-right: 3%;
  }
  .zhuq_twoli:last-child{
    border-bottom:none;
  }
  .more{
    font-size: 0.15rem;
    color: #888888;
    text-align: center;
    margin-top: 0.2rem;
    margin-bottom: 0.2rem;

  }
</style>



